$theme: 'buttercup';

$color-normal: #f1f1f1;
$color-dark: #000;
@if ($theme == 'blue') {
    $color-normal: rgb(186, 186, 214);
    $color-dark: rgb(98, 98, 231);
}
@if ($theme == 'buttercup') {
    $color-normal: #f1c40f;
    $color-dark: #f39c12;
}
@if ($theme == 'zest') {
    $color-normal: #e67e22;
    $color-dark: #d35400;
}

$color-athens-gray: #e7e9ec;
$color-golden-grass: #dd9a2d;
$color-white: #fff;
